<template>
  <a-spin :spinning="loading" :tip="$t('lang.tip')" size="large">
    <div :bordered="false">
      <transition name="fade">  
        <a-card style="background-color: #fff8db;margin-bottom: 10px;" v-show="show">
          <div class="table-page-search-wrapper">
            <a-form v-if="reset_flag" layout="inline">
              <a-row :gutter="48">
                <a-col :md="6" :sm="24">
                  <a-form-item 
                    :label="$t('lang.lang_type')"
                  >
                    <a-input v-model="queryParam.langType" :placeholder="$t('lang.lang_type_placeholder')"/>
                  </a-form-item>
                </a-col>
                <a-col :md="6" :sm="24">
                  <a-form-item 
                    :label="$t('lang.id')"
                  >
                    <a-input v-model="queryParam.id" :placeholder="$t('lang.id_placeholder')"/>
                  </a-form-item>
                </a-col>
                <a-col :md="6" :sm="24">
                  <a-form-item 
                    :label="$t('lang.content')"
                  >
                    <a-input v-model="queryParam.lang1" :placeholder="$t('lang.content_placeholder')"/>
                  </a-form-item>
                </a-col>
                <a-col :md="6" :sm="24">
                  <btn-search @click="$refs.table.refresh()" :showInput="false"></btn-search>
                  <a-button @click="onReset" style="margin-left: 8px">{{ $t('common.btn_reset') }}</a-button>
                </a-col>
              </a-row>
            </a-form>
          </div>
        </a-card>
      </transition>
      <a-card>
        <div class="table-page-search-wrapper">
          <a-row style="margin-bottom:10px;">
            <a-col :md="19" :sm="19">
              <btn-add @click="onAdd" ></btn-add>&nbsp;
              <btn-import
                action="/api/sys/lang/import"
                accept=".js"
                :beforeUpload="beforeUpload"
                @done="$refs.table.refresh();loading=false;"
                @uploading="loading=true;"
              ></btn-import>&nbsp;
              <btn-export
                @click="onExport"
              ></btn-export>
            </a-col>
            <a-col :md="5" :sm="5">
              <sorter @refresh="$refs.table.refresh()" @show="show = !show"></sorter>
            </a-col>
          </a-row>
          <s-table
            
            ref="table"
            :bordered="false"
            :autoLoad="true"
            :data="loadData"
            :rowKey="getKey"
            size="small"
          >
            <a-table-column 
              :title="this.$t('lang.index')"
              width="50px"
              align="center"
            >
              <span slot-scope="text">
                <a-icon type="appstore" theme="twoTone" class="icon" style="font-size:25px"/>
              </span>
            </a-table-column>
            <a-table-column
              :title="this.$t('lang.lang_type')"
              width="150px"
              dataIndex="langType"
            ></a-table-column>
            <a-table-column
              :title="this.$t('lang.id')"
              width="200px"
              dataIndex="id"
            ></a-table-column>
            <a-table-column
              :title="this.$t('lang.lang1')"
              width="250px"
              dataIndex="lang1"
            ></a-table-column>
            <a-table-column
              :title="this.$t('lang.lang2')"
              width="350px"
              dataIndex="lang2"
            ></a-table-column>
            <a-table-column
              :title="this.$t('lang.lang3')"
              dataIndex="lang3"
            ></a-table-column>
            <p
              slot="expandedRowRender" 
              slot-scope="record" 
              style="margin: 0;"
            >
              <a-list  
                :dataSource="langList"
              >
                <a-list-item slot="renderItem" slot-scope="item,index" style="padding: 0px;border:none;">
                  <a-row :index="index" style="width: 100%;">
                    <a-col :span="3" style="height: 40px;line-height:40px;">{{ item.name }}</a-col>
                    <a-col :span="7" style="height: 40px;;line-height:40px;">
                      <a-input :placeholder="$t('lang.content_placeholder')" v-model="record['lang' + item.langId]" />
                    </a-col>
                  </a-row>
                </a-list-item>
              </a-list>
              <a-row style="width: 100%;">
                <a-col :span="3" style="height: 40px;line-height:40px;"></a-col>
                <a-col :span="7" style="height: 40px;;line-height:40px;">
                  <btn-edit @click="onUpdate(record)" type="bt" :text="$t('common.btn_ok')"></btn-edit>&nbsp;
                  <btn-remove @click="onDelete(record)" type="bt"></btn-remove>
                </a-col>
              </a-row>
            </p>
          </s-table>
        </div>
      </a-card>
    </div>
    <lang-add
      ref="langAdd"
      @ok="$refs.table.refresh()" 
    ></lang-add>
  </a-spin>
</template>
<script>
  import { axios } from '@/utils/request'
  import STable from '@/components/table/'
  import LangAdd from '@/views/sys/lang/LangAdd'
  export default {
    name: 'Index',
    components: {
      STable,
      LangAdd
    },
    data() {
      return {
        show: true,
        reset_flag: true,
        loading: false,
        queryParam: {
          langType: '',
          id: '',
          lang1: ''
        },
        langList: [],
        loadData: (parameter) => {
          return axios({
            url: '/sys/lang/search-list',
            method: 'get',
            params: Object.assign(parameter, this.queryParam)
          }).then(data=>{       
            return data.result
          })
        }
      }
    },
    mounted(){
      this.loadLangList()
    },
    methods: {
      loadLangList(){
        const that = this
        axios({
          url: '/sys/lang/search-lang-list',
          method: 'get',
          params: {}
        }).then(data=>{    
          that.langList = data.result
        })
      },
      onAdd(){
        this.$refs.langAdd.showModal()
      },
      onUpdate(record){
        const that = this
        axios({
          url: '/sys/lang/update',
          method: 'post',
          params: record
        }).then(result=>{
          if(result){
            that.$message.success(that.$t('lang.msg1'))
          }
        })
      },
      onDelete(record){
        const that = this
        axios({
          url: '/sys/lang/delete',
          method: 'post',
          params: record
        }).then(result=>{
          if(result){
            that.$message.success(that.$t('lang.msg1'))
            that.$refs.table.refresh()
          }
        })
      },
      onReset(){
        const that = this
        that.queryParam = {
          langType: '',
          id: '',
          lang1: ''
        }
        that.reset_flag = false
        this.$nextTick(() => {
          that.reset_flag = true
        })
      },
      getKey(record){
        return record.langType + record.id
      },
      beforeUpload(file){
        let exits = false
        let types = ''
        for(let i=0; i<this.langList.length; i++) {
          types += '[' + this.langList[i].isoCode + '] '
          if(this.langList[i].isoCode + '.js' == file.name){
            exits = true
            break
          }
        }
        if(!exits){
          this.$message.error(this.$t('lang.msg2') + types, 10)  
          return false
        }
        return true
      },
      onExport(){
        const that = this
        that.loading = true
        axios({
          url: '/sys/lang/export',
          method: 'get',
          responseType: 'blob'
        }).then(result=>{
          that.loading = false
          const url = window.URL.createObjectURL(new Blob([result]))
          const link = document.createElement('a')
          link.style.display = 'none'
          link.href = url
          link.setAttribute('download', 'langs.zip')
          
          document.body.appendChild(link)
          link.click()
        })
      }
    }
  }
</script>