<template>
  <a-modal
    :maskClosable="false" 
    :bordered="false"
    :visible="visible"
    :destroyOnClose="true"
    :title="$t('lang.title1')"
    @ok="onSubmit"
    width="650px"
    @cancel="visible=false"
  >
    <a-spin :spinning="loading">
      <a-form :form="form">
        <a-form-item
          :label="$t('lang.lang_type')"
          v-bind="formItemLayout"
        >
          <a-input 
            :placeholder="$t('lang.lang_type_placeholder')"
            v-decorator="[
              'langType',
              {initialValue: '', rules:[{required: true}]}
            ]"
          />
        </a-form-item> 
        <a-form-item
          :label="$t('lang.id')"
          v-bind="formItemLayout"
        >
          <a-input 
            :placeholder="$t('lang.id_placeholder')"
            v-decorator="[
              'id',
              {initialValue: '', rules:[{required: true}]}
            ]"
          />
        </a-form-item> 
        <div v-for="(item,index) in langList" :key="item.langId">
          <a-form-item
            :label="item.name"
            :index="index"
            v-bind="formItemLayout"
          >
            <a-input 
              :placeholder="$t('lang.content_placeholder')"
              v-decorator="[
                'lang' + item.langId,
                {initialValue: '', rules:[{required: true}]}
              ]"
            />
          </a-form-item> 
        </div>
      </a-form>
    </a-spin>
  </a-modal>
</template>
<script>
import { axios } from '@/utils/request'
export default {
  data() {
    return {
      visible: false,
      loading: false,
      form: this.$form.createForm(this),
      formItemLayout: {labelCol: {lg: {span: 6}, sm: {span: 24}},wrapperCol: {lg: {span: 16}, sm: {span: 24}}},
      langList: []
    }
  },
  methods: {
    showModal(){
      if(this.langList.length == 0){
        this.loadLangList()
      }
      this.visible = true
    },
    loadLangList(){
      const that = this
      axios({
        url: '/sys/lang/search-lang-list',
        method: 'get',
        params: {}
      }).then(data=>{    
        that.langList = data.result
      })
    },
    onSubmit(){
      this.form.validateFields((err,values) => {
        if (!err) {
          const that = this
          that.loading = true
          axios({
            url: '/sys/lang/insert',
            method: 'post',
            params: values
          }).then(result=>{
            that.loading = false
            if(result){
              this.visible = false
              this.$emit('ok',result)
              that.$message.success(that.$t('lang.msg1'))
            }
          })
        }
      })
    }
  }
}
</script>